.editProfile-container {
  .editProfile__title {
    font-size: 32px;
  }
  .editProfile__input {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: inherit;
    padding: 0 15%;
    .editProfile__avatar,
    .editProfile__name,
    .editProfile__email {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;
      padding-bottom: 5px;
      border-bottom: 1px solid $borderBottom;
    }
    .editProfile__name,
    .editProfile__email {
      input {
        outline: none;
        border: none;
        border-radius: 5px;
        width: 40%;
        height: 30px;
        font-size: 20px;
        text-align: center;

        &::placeholder {
          text-align: center;
        }
      }
    }
    .editProfile__buttons {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      > * {
        margin-bottom: 30px;
      }
      input {
        @include button1(gray);
      }
      a {
        @include button1(red);
      }
    }
  }
}
