.header {
  background-color: $bgColor;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: fixed;
  border: 2px solid $borderBottom;
  border-top: none;
  top: 0;
  width: 100vw;
  height: 80px;
  z-index: 3;
  .header__column {
    height: 100%;
    &:first-child {
      width: 33%;
      display: flex;
      align-items: center;
      form {
        width: 100%;
        height: 50%;
        display: flex;
        align-items: center;
      }
      input {
        outline: none;
        width: 50%;
        height: 100%;
        border: none;
        border-radius: 5px;
        transition: width 0.2s ease-in-out;
        &:focus {
          width: 100%;
        }
      }
    }
    &:nth-child(2) {
      width: 33%;
      display: flex;
      justify-content: center;
      align-items: center;
      > a {
        height: inherit;
        font-size: 50px;
        text-decoration: none;
        color: black;
        &:hover {
          animation: bounce 0.8s ease-in-out;
        }
        img {
          height: inherit;
        }
      }
    }
    &:nth-child(3) {
      width: 33%;
      ul {
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-right: 30px;
        li {
          margin-left: 20px;
          display: flex;
          align-items: center;
          a {
            color: black;
            font-size: 20px;
            font-weight: 600;
            text-decoration: none;
          }
        }
      }
    }
  }
}

@keyframes bounce {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: scale(0.9) rotateY(180deg);
  }
  100% {
    transform: rotateY(3600deg);
  }
}
