.videos__block {
  background-color: $gray;
  width: 70vw;
  padding: 10px;
  .videos__description {
    font-size: 20px;
    margin-bottom: 10px;
    border-bottom: 1px solid rgba(187, 187, 187, 0.616);
  }
  .videos {
    display: grid;
    grid-template-rows: 300px;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-auto-rows: 300px;
    justify-content: center;
    gap: 5px;
  }
}
