.social-login {
  display: flex;
  align-items: center;
  button {
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 5px;
    height: 45px;
    width: 300px;
    margin: 10px;
    font-size: 16px;

    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    &:not(.social-login--kakaotalk) {
      a {
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      i {
        font-size: 26px;
        margin-right: 5px;
      }
    }
    &:hover {
      opacity: 0.9;
    }
    &:active {
      opacity: 0.4;
    }
    &.social-login--github {
      background-color: rgb(199, 199, 199);
      a {
        color: black;
      }
    }
    &.social-login--facebook {
      background-color: #415e9a;
      a {
        color: white;
      }
    }
    &.social-login--kakaotalk {
      background-color: rgba(255, 255, 255, 0);
      padding: 0;
    }
  }
}
