.form-container__edit {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  form {
    display: grid;
    width: 50vw;
    height: 80vh;
    background-color: $gray;
    border: 1px solid rgb(243, 243, 243);
    grid-template-rows: 4fr 1fr 1fr 1fr;
    grid-template-columns: 1fr;
    justify-items: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 30px;

    > * {
      margin-bottom: 5px;
    }
    .record-container {
      width: 80%;
      display: flex;
      flex-direction: column;
      align-items: center;

      video {
        width: 100%;
        background-color: black;
        margin-bottom: 5px;
      }
      button {
        @include button1(gray);
      }
    }
    .video__player {
      display: flex;
      justify-content: center;
      align-items: center;
      video {
        width: 100%;
      }
    }
    .title,
    .description,
    .video {
      display: grid;
      grid-template-columns: 1fr 9fr;
      justify-self: flex-start;
      align-items: center;
      width: 100%;

      label {
        margin-right: 10px;
      }
      input,
      textarea {
        width: 100%;
        outline: none;
        border: 1px solid rgba(0, 0, 0, 0.404);
        border-radius: 5px;
      }
      textarea {
        height: 100px;
      }
    }
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      input,
      a {
        margin-right: 5px;
        @include button1(gray);
      }
      a {
        @include button1(red);
      }
    }
  }
}
