.video_detail {
  display: grid;
  grid-template-rows: 100vh;
  background-color: $gray;
  grid-template-columns: 5fr 2fr;
  .video__block {
    box-sizing: border-box;
    padding: 10px 5vw;
    padding-right: 5px;
    width: 100%;
    display: flex;
    flex-direction: column;
    > * {
      margin-bottom: 10px;
    }

    .video__info {
      > * {
        margin-bottom: 10px;
      }
      h5 {
        font-size: 20px;
      }
      .video__sub {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid rgb(165, 165, 165);
        a {
          @include button1(gray);
          margin-bottom: 5px;
        }
      }
      .video__author {
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgb(165, 165, 165);
        padding-bottom: 10px;
        img {
          width: 60px;
          height: 60px;
          border: 1px solid rgba(0, 0, 0, 0.404);
          border-radius: 100%;
          margin-right: 10px;
        }
        a {
          text-decoration: none;
          color: rgb(11, 46, 145);
          font-size: 15px;
        }
      }
    }
  }
  .video__comments {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 20px 5px;
    > span {
      margin-bottom: 10px;
    }
    .video__comments-input {
      display: flex;
      width: 100%;
      align-items: center;
      padding-bottom: 5px;
      border-bottom: 1px solid $borderBottom;
      img {
        width: 60px;
        height: 60px;
        border-radius: 30%;
        background-color: white;
        margin-right: 10px;
      }
      form {
        width: 100%;
      }
      input {
        width: 90%;
        height: 24px;
        outline: none;
        border: none;
        border-radius: 5px;
        padding: 0;
      }
    }
    .video__comments-texts {
      width: 100%;
      height: 70vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      overflow: scroll;
      padding-top: 5px;
      .video__comments-text {
        display: flex;
        width: 100%;
        align-items: center;
        margin-bottom: 10px;

        .video__comments-column {
          &:first-child {
            img {
              width: 60px;
              height: 60px;
              border-radius: 30%;
              background-color: white;
              margin-right: 10px;
            }
          }
          &:nth-child(2) {
            width: 70%;
            position: relative;
            overflow-wrap: break-word;
            &:hover {
              .video__comments-delete {
                opacity: 1;
              }
            }

            li {
              box-sizing: border-box;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              margin-bottom: 5px;
              padding: 5px 10px;
              width: 100%;
              border-radius: 5px;
              background-color: white;
              word-break: break-all;
            }
            .video__comments-info {
              display: flex;
              justify-content: space-between;
              align-items: flex-end;
              span {
                margin-bottom: 3px;

                &:nth-child(2) {
                  font-size: 12px;
                }
              }
            }
            button.video__comments-delete {
              position: absolute;
              right: 0;
              top: 20px;
              opacity: 0;
              outline: none;
              border: none;
              background-color: #b10214;
              border-radius: 5px;
              width: 30px;
              height: 30px;
              font-weight: 800;
              cursor: pointer;
              &:hover {
                opacity: 0.8;
              }
              &:active {
                opacity: 0.5;
              }
            }
          }
        }
      }
    }
  }
}
