@mixin button1($color) {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;

  @if $color ==gray {
    background-color: $buttonColor;
  } @else if $color == red {
    background-color: rgb(129, 55, 55);
  } @else {
    background-color: none;
  }
  color: white;
  height: 30px;
  padding: 0 10px;
  outline: none;
  font-size: 12px;

  cursor: pointer;
  &:hover {
    opacity: 0.9;
  }
  &:active {
    opacity: 0.6;
  }
}
