.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  form {
    display: grid;
    width: 50vw;
    height: 80vh;
    background-color: $gray;
    border: 1px solid rgb(243, 243, 243);
    grid-template-rows: 1fr 1fr 9fr;
    grid-template-columns: 1fr;
    justify-items: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    span.form_title {
      font-size: 50px;
      font-weight: 800;
    }
    span.form_description {
      font-size: 20px;
      font-weight: 600;
    }
    .form_block {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;

      > div {
        margin-bottom: 15px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      label {
        display: flex;
        font-size: 32px;
        justify-content: center;
        margin-bottom: 3px;
      }
      input {
        outline: none;
        border: none;
        border-radius: 5px;
        height: 40px;
        width: 60%;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
      }
      input[type="submit"] {
        cursor: pointer;
        font-size: 20px;
        background-color: rgb(185, 158, 153);
        color: white;
        width: 40%;

        &:hover {
          opacity: 0.9;
        }
        &:focus {
          opacity: 0.4;
        }
      }
    }
  }
}
