.changePassword-container {
  .changePassword-title {
    font-size: 32px;
  }
  .changePassword-inputs {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: inherit;
    padding: 0 15%;

    > *:not(.changePassword-submit) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 30px;
      padding-bottom: 5px;
      border-bottom: 1px solid $borderBottom;
      input {
        outline: none;
        border: none;
        border-radius: 5px;
        width: 40%;
        height: 30px;
        font-size: 20px;
        text-align: center;
      }
    }
    .changePassword-submit {
      display: flex;
      justify-content: center;
      input {
        @include button1(gray);
      }
    }
  }
}
