.videoBlock {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;

  a {
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: black;
    video {
      width: 100%;
      height: 100%;
      outline: none;
    }
  }
  h4,
  h6 {
    display: flex;
    justify-content: center;
    margin-top: 5px;
  }
  h4 {
    font-size: 15px;
  }
  h6 {
    font-size: 12px;
  }
}
