.video__player {
  width: 100%;
  overflow: hidden;

  .video__player-video {
    width: 100%;
    position: relative;
    video {
      width: 100%;
      max-height: 100%;
    }
    .video__player-controls {
      position: absolute;
      box-sizing: border-box;
      bottom: 2px;
      color: white;
      display: grid;
      width: 100%;
      grid-template-columns: repeat(3, 1fr);
      justify-items: center;
      padding: 5px 10px;
      color: white;
      font-size: 25px;
      background-color: rgba(56, 50, 77, 0.486);
      opacity: 0;
      transition: opacity 0.2s ease-in-out;
      .video__player-columns {
        display: flex;
        align-items: center;
        position: relative;
        width: 100%;
        .video__player-button {
          cursor: pointer;
          :hover {
            opacity: 0.8;
          }
        }

        &:first-child {
          justify-self: start;
          > * {
            margin-right: 5px;
          }
          .video__player-time {
            font-size: 13px;
          }
          .video__player-volume {
            display: flex;
            .video__player-volumeBar {
              -webkit-appearance: none;
              background: rgba(56, 50, 77, 0.486);
              height: 25px;
              width: 0px;
              transform: rotateZ(-90deg);
              outline: none;
              position: absolute;
              visibility: hidden;
              bottom: 86px;
              left: -23px;
              opacity: 0;
              transition: all 0.3s ease-in-out;

              &::-webkit-slider-thumb {
                -webkit-appearance: none;
                height: 10px;
                width: 10px;
                border-radius: 50%;
                background: white;
                cursor: pointer;
                margin-top: -2px; /*chrome의 경우 margin-top값을 부여해야합니다.*/
                padding-right: 10px;
              }
              &::-webkit-slider-runnable-track {
                cursor: pointer;
                width: 100%;
                height: 5px;
                margin: 5px;
                background: #ffffff4b;
              }
            }
          }
        }
        &:nth-child(2) {
          display: flex;
          justify-content: center;
        }
        &:nth-child(3) {
          display: flex;
          justify-content: flex-end;
        }
      }
      .video__player-timeBar {
        position: absolute;
        width: 100%;
        bottom: 34px;
        -webkit-appearance: none;
        background: rgba(49, 43, 70, 0.432);
        height: 30px;
        outline: none;
        position: absolute;

        &::-webkit-slider-thumb {
          -webkit-appearance: none;
          height: 10px;
          width: 10px;
          border-radius: 50%;
          background: white;
          cursor: pointer;
          margin-top: -2px; /*chrome의 경우 margin-top값을 부여해야합니다.*/
          padding-right: 10px;
        }
        &::-webkit-slider-runnable-track {
          cursor: pointer;
          width: 100%;
          height: 5px;
          margin: 5px;
          background: #ffffff4b;
        }
      }
    }
  }
}
